.div-border {
  background: white;
  padding: 40px;
  border-radius: 12px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 380px;
  border: 1px solid #e5e7eb;
}

.div-border h2 {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 24px;
  color: #374151;
  text-align: center;
}

.div-border form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.form-div {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
}

.div-border label {
  font-size: 0.875rem;
  font-weight: 500;
  color: #4b5563;
  margin-bottom: 4px;
}

.div-border input[type="text"],
.div-border input[type="number"] {
  padding: 10px;
  border: 0;
  border-bottom: 1px solid #d1d5db;
  font-size: 1rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  width: 100%;
}

.div-border input[type="text"]:focus,
.div-border input[type="number"]:focus {
  border-bottom: 1px solid #3b82f6;
  outline: none;
  box-shadow: 0 0 0 2px rgba(59, 130, 246, 0.25);
}

.div-border .form-group {
  display: flex;
  flex-direction: column;
}

.div-border .price-display {
  color: #4b5563;
  padding-top: 8px;
  text-align: center;
}

.div-border .price-value {
  font-weight: 600;
  color: #2563eb;
}

.div-border button {
  width: 100%;
  background-color: #3b82f6;
  color: white;
  font-weight: 600;
  padding: 12px 0;
  border-radius: 12px;
  border: none;
  cursor: pointer;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: background-color 0.2s, box-shadow 0.2s;
  font-size: 1rem;
}

.div-border button:hover {
  background-color: #2563eb;
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15);
}

.div-border button:focus {
  outline: none;
  box-shadow: 0 0 0 4px rgba(96, 165, 250, 0.5);
}
